<!-- https://www.koderhq.com/tutorial/vue/http-fetch/ -->

<template>
  <div class="search-wrapper panel-heading col-sm-12">
    <input type="text" v-model="search" placeholder="Rechercher" /> <br />
    <br />
  </div>
  <li>
    <button v-on:click="setCat('')">Aucun</button>
  </li>
  <li v-for="cat in categories" :key="cat.id">
    <button v-on:click="setCat(cat.category)">
      {{ cat.category }}
    </button>
  </li>
  <hr />
  <div v-if="isLoading" class="lds-dual-ring"></div>
  <div v-for="p in filteredPosts" :key="p.id">
    <a href="#">
      <h2>{{ p.id }} - {{ p.title }}</h2>
    </a>
    <a href="#">
      <p>{{ p.category }}</p>
    </a>
    <p>{{ p.body }}</p>
  </div>
</template>

<script>
// import { getPosts } from './FetchData.js';

export default {
  name: 'Articles',

  data() {
    return {
      post: [],
      isLoading: false,
      search: '',
      category: '',
      categories: [],
    };
  },
  // check URL's query params
  created() {
    let qp = new URLSearchParams(window.location.search);
    let f = qp.get('category');
    if (f) this.category = qp.get('category');
  },
  methods: {
    getPosts() {
      this.isLoading = true;
      // fetch('https://jsonplaceholder.typicode.com/posts/')
      fetch('https://api.npoint.io/7114a70b681e87f0eb24')
        .then((response) => response.json())
        .then((data) => {
          this.post = data;
          this.isLoading = false;
        });
    },
    getCat() {
      fetch('https://api.npoint.io/b7958756c9179e8a6d24')
        .then((response) => response.json())
        .then((data) => {
          this.categories = data;
          console.log(this.categories);
        });
    },
    setCat(cat) {
      this.category = cat;
      console.log(cat);
    },
  },
  computed: {
    //searchBar
    filteredPosts() {
      return this.post.filter((p) => {
        // if (!this.category.length)
        // return p.title.toLowerCase().indexOf(this.search.toLowerCase()) != -1;
        let valid = false;
        if (p.title.toLowerCase().indexOf(this.search.toLowerCase()) != -1)
          valid = true;
        if (this.category == '' || this.category == 'None') return valid;
        if (valid && p.category.toLowerCase() == this.category.toLowerCase())
          return true;
      });
    },
  },
  mounted() {
    this.getPosts();
    this.getCat();
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.lds-dual-ring:after {
  content: ' ';
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid rgb(0, 0, 0);
  border-color: rgb(68, 68, 68) transparent rgb(68, 68, 68) transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
</style>
